html {
    height: 100%;
    overflow-y: scroll;
    overflow-x: hidden;
}
body {
    height: 100%;
    display: flex;
    flex-direction: column;
    font-family: $font-family;
    font-size: 16px;
    background:$light-black;
    color: #676767;
}

h1 {
    font-size: 3rem;
    color: $turquoise;
    letter-spacing: 0.2rem;
}

a {
    text-decoration: none;
    color: $dark-white;
    &:visited {
        color: $turquoise;
    }
    &:hover,
    &:active {
        text-decoration: none;
        opacity: 0.7;
    }
}

ul {
    padding-inline-start: 0px;
}